@use "sass:meta";
@tailwind base;
@tailwind components;
@tailwind utilities;

@include meta.load-css("react-date-range/dist/styles.css");
@include meta.load-css("react-date-range/dist/theme/default.css");

body {
    min-width: 320px;
}

.text-decoration-none {
    text-decoration: none !important;
}

@include meta.load-css("loader");
@include meta.load-css("facet");
@include meta.load-css("custom-date-range");
@include meta.load-css("reddit-comment");

input[type="search"]::-webkit-search-cancel-button {
    /* Remove default */
    -webkit-appearance: none;
    /* Now your own custom styles */
    height: 1.25rem;
    width: 1.25rem;
    display: block;
    background-image: url(data:image/svg+xml;base64,);
    /* setup all the background tweaks for our custom icon */
    background-repeat: no-repeat;
    background-size: 1.25rem;
    cursor: pointer;
}
